import { getUser, getMenuUser, bindingPhone } from "@/api/user";
import { isWeixin, VUE_APP_RESOURCES_URL } from "@/utils";
import SwitchWindow from "@/components/SwitchWindow";
import { mapGetters } from "vuex";

const NAME = "User";

export default {
  name: NAME,
  components: {
    SwitchWindow
  },
  props: {},
  data: function() {
    return {
      userInfo: {},
      MyMenus: [],
      orderStatusNum: {},
      switchActive: false,
      isWeixin: false
    };
  },
  computed: mapGetters(["wxCode"]),
  watch: {
    $yroute(n) {
      if (n.name === NAME) this.User();
    }
  },
  mounted: function() {
    console.log('这个是个人中心')
    this.User();
    this.MenuUser();
    this.isWeixin = isWeixin();
  },
  methods: {
    getPhoneNumber: function(e) {
      console.log(e.mp.detail);
      if (e.mp.detail.errMsg == "getPhoneNumber:ok") {
        wx.showLoading({ title: "绑定中" });
        wx.login({
          success: loginRes => {
            bindingPhone({
              code: loginRes.code,
              encryptedData: e.mp.detail.encryptedData,
              iv: e.mp.detail.iv
            })
              .then(res => {
                this.User();
                wx.hideLoading();
                wx.showToast({
                  title: res.msg,
                  icon: "success",
                  duration: 2000
                });
              })
              .catch(error => {
                wx.showToast({
                  title: error.msg || error.response.data.msg,
                  icon: "none",
                  duration: 2000
                });
              });
          }
        });
      } else {
        wx.showToast({
          title: "已拒绝授权",
          icon: "none",
          duration: 2000
        });
      }
    },
    changeswitch: function(data) {
      this.switchActive = data;
    },
    User: function() {
      let that = this;
      getUser().then(res => {
        that.userInfo = res.data;
        that.orderStatusNum = res.data.orderStatusNum;
      });
    },
    MenuUser: function() {
      let that = this;
      getMenuUser().then(res => {
        that.MyMenus = res.data.routine_my_menus;
      });
    },
    goPages: function(index) {
      let url = this.MyMenus[index].wxapp_url;
      if (
        url === "/pages/user/promotion/UserPromotion/main" &&
        this.userInfo.statu === 1
      ) {
        if (!this.userInfo.isPromoter)
          return this.$dialog.toast({ mes: "您还没有推广权限！！" });
      }

      if (
        url === "/pages/orderAdmin/OrderIndex/main" &&
        !this.userInfo.adminid
      ) {
        return this.$dialog.toast({ mes: "您还不是管理员！！" });
      }

      this.$yrouter.push({ path: this.MyMenus[index].wxapp_url });
    }
  },
  onShow() {
    this.User();
    this.MenuUser();
    this.isWeixin = isWeixin();
  }
};
